  /* Style the hamburger menu */
  #burger {
    background: black;
    display: flex;
    border:1px solid white;
    padding: 1rem;
    border-radius: 5px;
    justify-content: flex-start;
    margin-top: 1rem;
    position:fixed;
    z-index: 5;
    margin-left:1rem;
    
  }
  .fa {
    color: white;
  }
  
  /* Add a grey background color on mouse-over */
  #burger:hover {
    background-color: #ddd;
    color: black;
    .fa{
        color:black;
    }
  }

  /* Style the navigation menu */
#myLinks {
  display: none;
    overflow: hidden;
    position: relative;
  }
  
